import React, {Component} from "react";
import PropTypes from "prop-types";
import {OraButton, OraLoader, OraMessage} from "eshop/components/OraCommonComponents";
import {connect} from "react-redux";
import {getCustomerMainAddressForm} from "../../selectors";
import {changeCustomerMainAddressFormField} from "../../actions/app";
import {requestCartCustomerData} from "../../actions/data";
import MulticartCommonAddressForm from "../MulticartCommonAddressFormComponent"

/* Presentational Component - accepts props from container component like MulticartCommonAddressFormView Component and uses them for render
 * */

export class MulticartBillingAddressView extends Component {
    componentDidMount() {
        this.props.requestCartCustomerData();
    }

    getTitle() {
        if(this.props.isBusinessClient) {
            return this.props.descriptions.businessTitle || this.props.defaultBusinessTitle || "";
        } else if(this.props.foreigner) {
            return this.props.descriptions.titleForeigner;
        } else {
            return this.props.descriptions.title || ""
        }
    }

    handleChange({id, name, value, cbsId}) {
        this.props.changeCustomerMainAddressFormField({id, name, value, cbsId}, false);
    }

    render() {
        return this.props.show ? (
            <div className="opl-form u-spacing-l u-clearfix">
                <div className={`u-spacing-top-l ${this.props.isForeignerCheckboxAvailable ? 'u-large-spacing-xl u-medium-spacing-s u-small-spacing-s' : ''} l-col l-col-small-12 l-col-medium-12 l-col-12 u-no-padding`}>
                    <p className="h3" dangerouslySetInnerHTML={{ __html: this.getTitle()}}/>
                </div>
                <MulticartCommonAddressForm {...this.props} onChange={this.handleChange.bind(this)} onBlur={this.props.changeCustomerMainAddressFormField} id={"main"}/>
            </div>
        ) : <div/>
    }
}

MulticartBillingAddressView.propTypes = {
    postalCode: PropTypes.string,
    town: PropTypes.string,
    streetName: PropTypes.string,
    streetNumber: PropTypes.string,
    appartmentNo: PropTypes.string,
    citySuggestions: PropTypes.array,
    streetSuggestions: PropTypes.array,
    foreigner: PropTypes.bool,
    readOnly: PropTypes.bool,
    requestCartCustomerData: PropTypes.func,
    changeCorrespondenceAddressFormField: PropTypes.func
};

MulticartBillingAddressView.defaultProps = {
    defaultBusinessTitle: "Adres siedziby firmy"
};

const MulticartBillingAddressComponent = connect(
        getCustomerMainAddressForm,
        {changeCustomerMainAddressFormField, requestCartCustomerData}
    )(MulticartBillingAddressView);
export default MulticartBillingAddressComponent;
